* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "anybody-regular";
}

body {
  background: #1a1b1c;
  border: 20px solid #f3e500;
  border-bottom: 0;
  min-height: 100vh;
  overflow-x: hidden;
  overflow-x: hidden !important;
  display: flex;
  flex-direction: column;
}

.body::-webkit-scrollbar {
  transition: all 0.3s ease;
}

